.btn {
    font-size: 14px !important;
    border: none !important;
    &:hover {
        box-shadow: none !important;
        border: none !important;
    }
    &:focus {
        box-shadow: none !important;
    }
}
.btn-primary {
    &:hover {
        background: rgba(255, 255, 0, 0.5) !important;
        color: #007bff;
    }
}
