.navbar-brand {
    img {
        width: 120px;
    }
}
footer {
    background: rgba(255, 159, 64, 0.2);
}

// run-feed-popup

.run-feed-popup {
    position: relative;
    background: rgb(236, 236, 236);

    &:after {
        content: '';
        position: absolute;
        top: -23px;
        left: 50%;
        transform: translateX(-50%) rotate(45deg);
        width: 45px;
        height: 45px;
        background: rgb(236, 236, 236);
    }
}

// run-feed-popup

.bid-select {
    ul {
        li {
            margin-right: 0 !important;

            a {
                display: inline-block;
                width: 45px;
                height: 45px;
                border-radius: 15px;
                background: #0062cc;
                text-align: center;
                margin: 2.5px 0;
                line-height: 45px;
                color: #fff;

                &:hover {
                    background: #0b54a3;
                }
            }

        }
    }
}

.pass {
    width: 95px !important;
    background: #1d3a58 !important;
}

.bid-select-active {
    background: #0063cc81 !important;
}

.bg-active {
    background: #fff;
}

.deposit-form-control {
    background: #dcdcdc !important;
}

// social-icon
.social-icon {
    li {
        a {
            background: #000;
            width: 35px;
            height: 35px;
            line-height: 35px;
            display: inline-block;
            text-align: center;
            border-radius: 50%;
            color: #fff;
            background: #0062cc;
        }
    }
}