
a,a:hover,a:focus{
    text-decoration: none;
}

.py-25 {
    padding: 25px 0;
}
.px-25 {
    padding: 0 25px;
}
.py-50 {
    padding: 50px 0;
}
.px-50 {
    padding: 0 50px;
}
.py-75 {
    padding: 75px 0;
}
.px-75 {
    padding: 0 75px;
}
.py-100 {
    padding: 100px 0;
}
.px-100 {
    padding: 0 100px;
}
.section {
    padding-top: 100px;
    padding-bottom: 100px;
}
.text-secondary {
    color: red !important;
    &:hover {
        color: limegreen !important;
    }
}
.text-green {
    color: limegreen;
}
.text-red {
    color: red;
}
.bg-primary {
    background: rgba(255,255,0,0.5) !important;
}
.transition {
    transition: ease 0.3s;
}
.cursor-pointer {
    cursor: pointer;
}
